@value colors: "../../components/colors.module.css";
@value primaryMain, grey0, grey50, grey100, grey200, grey300, grey400, grey500, grey600, grey700, grey800, grey900 from colors;

.startSection header {
  margin: 0 80px;
  padding: 16px 0;
}

.startSection .logo {
  width: 48px;
  height: 48px;
  background-color: primaryMain;
}

.startSection > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 80px;
  border-bottom: 1px dashed primaryMain;
  min-height: 800px;
}

.startSection .content p:nth-child(1) {
  margin-bottom: 16px;
}

.startSection .content p:nth-child(3) {
  margin: 24px 0 32px 0;
  max-width: 90%;
}

.startSection .content button:nth-child(5) {
  margin-left: 16px;
}

.startSection .image {
  flex-shrink: 0;
  margin-left: 40px;
  width: 400px;
  height: 400px;
  background-color: #ccc;
}
